import "./App.css";
import React, { useRef, Suspense, useState, useEffect } from "react";
import Animation from "./Animation";
import Box from "@material-ui/core/Box";
import Backdrop from "@material-ui/core/Backdrop";
import Copy from "./Copy";
import {
  createTheme,
  ThemeProvider,
  responsiveFontSizes,
} from "@material-ui/core/styles";
import Bio from "./Bio";
import { CssBaseline, Slider } from "@material-ui/core";
import { InView } from "react-intersection-observer";
import Header from "./Header";
import Main from "./Main";
import Contact from "./Contact";
import Crop from "./Slider";
import { ParallaxProvider } from "react-scroll-parallax";
import Loader from "./Loader";
let theme = createTheme({
  palette: {
    type: "dark",
    background: {
      default: "#0d0d12",
    },
  },
  typography: {
    body1: {
      color: "#b0b0b0",
    },
    h1: {
      fontWeight: 700, // or 'bold'
      fontSize: [70, "!important"],
      color: "#ededed",
    },
    h2: {
      color: "#bd4545",
    },
    fontFamily: [
      "objektiv-mk3",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(","),
  },
});
theme = responsiveFontSizes(theme);

// Check viewport visibility

function App() {
  const [index, setIndex] = useState();
  const [loaded, setLoaded] = useState();

  const Progress = (data) => {
    useEffect(() => {
      setLoaded(data === 100 ? true : false);

      console.log(data);
      console.log(loaded);
    }, [data]);
  };

  return (
    <ThemeProvider theme={theme}>
      {loaded || <Loader />}
      <ParallaxProvider>
        <CssBaseline />
        <Header />
        <Box
          position="fixed"
          width="100%"
          height="100%"
          top="0"
          zIndex="drawer"
        >
          <Animation index={index} progress={Progress} />
        </Box>
        <InView
          as="div"
          threshold={0.3}
          onChange={(inView2, entry) => inView2 && setIndex(2)}
        >
          <Main />
        </InView>
        <Crop copy={"PORTFOLIO"} />
        <InView
          as="div"
          threshold={0.3}
          onChange={(inView, entry) => inView && setIndex(5)}
        >
          <Bio header="Key Visuals" />
        </InView>
        <Bio className="App-header noPointer" right={true} header="Logotypes" />
        <InView
          as="div"
          threshold={0.3}
          onChange={(inView, entry) => inView && setIndex(1)}
        >
          <Bio header="Jak pracuję" />
        </InView>
        <InView
          as="div"
          threshold={0.3}
          onChange={(inView, entry) => inView && setIndex(0)}
        >
          {" "}
          <Crop copy={"KONTAKT"} />
          <Contact />
        </InView>
      </ParallaxProvider>
    </ThemeProvider>
  );
}

export default App;
