import { Box, Grid } from "@material-ui/core";
import React from "react";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  loader: {
    backgroundColor: "#242624",
  },
  center: {
    textAlign: "center",
  },
  svg: {
    width: "25%",
  },
}));

function Loader() {
  const classes = useStyles();
  return (
    <Box
      className={classes.loader}
      position="fixed"
      width="100%"
      height="100%"
      top="0"
      zIndex="1600"
      alignItems="center"
      display="flex"
    >
      <Grid
        container
        direction="row"
        justifyContent="center"
        alignItems="center"
      >
        <Grid item xs={4} sm={4} alignItems="center" className={classes.center}>
          <img src="/hand.svg" className={classes.svg} />
        </Grid>
      </Grid>
    </Box>
  );
}

export default Loader;
